function loadStyleSheet (displayname, name = displayname, options = {}) {
    const ptss = textplain.ptssParser.loadSheet(name, options)
    console.log(`loading stylesheet ${name}${JSON.stringify(options)} as "${displayname}"`)
    const reader = new textplain.ptssParser.Parser(ptss)
    stylesheets[displayname] = { reader: reader }
}

function setStyleSheet (name) {
    console.log(`setting stylesheet: "${name}"`)
    reader = stylesheets[name].reader
}

async function updatePipeline (pipeIDs) {
    pipes = []
    for (const pipeID of pipeIDs) {
        const cfg = pipeConfigs[pipeID]
        const pipe = await textplain.configPipeProvider(cfg)
        if (pipe !== undefined) {
            pipes.push(pipe)
        }
    }
    parsePipeAndRender()
}

function getQueryVariable (variable) {
    const query = window.location.search.substring(1)
    const vars = query.split('&')
    for (let i = 0; i < vars.length; i++) {
        const pair = vars[i].split('=')
        if (pair[0] === variable) {
            return decodeURIComponent(pair[1])
        }
    }
    return null
}

/**
 * //todo[doc] the rules for decorator in view-custom-smartly-prettified-object package
 *
 * @param node
 * @return {boolean}
 */
const irDecorator = function (node) {
    //console.log('decorator', node.key, node.value, node)

    // todo[api] should this be default behavior for viewCustomSmartlyPrettifiedObject ?
    if (node.type === 'function') return false

    // represent the semantics of undefined padding
    if (node.key === 'padding' && node.val === undefined) node.val = 'variable'

    //! we can skip empty vals at this point because
    //   none of the remaining rules replace them
    if (node.val === null) return false
    if (node.val === undefined) return false

    /////////////////////  nodes to skip   (do this after all rewriting)
    if (node.type === 'function') return false
    if (node.key === 'lastLineBlank' && node.val === false) return false
    if (node.key === 'endItemIfNextLineBlank' && node.val === false) return false

    // hide temp keys
    if (node.key && node.key[0] === '~') return false // temp var, not STIR spec

    // hide tunnel
    if (node.key === '_tunnel') return false

    // no point in showing parent key as a branch since it always points the node's direct parent.
    // todo![now] viewCustomSmartlyPrettifiedObject decorator returning
    //   string for branchNode value is rendered wrong.
    // if (node.key === 'parent' && node.val) node.val = node.val.type
    // UNTIL the above is fixed, do this:
    if (node.key === 'parent' && node.val) node.descriptor = node.val.type

    ////////////////////  move IR type into descriptor
    if (node.val['type']) node.descriptor = node.val['type']
    if (node.val['_type']) node.descriptor = node.val['_type']
    // skip type key if we're already showing it in the parent descriptor
    if (node.key === 'type' && node.parent.descriptor === node.val) return false
    if (node.key === '_type' && node.parent.descriptor === node.val) return false

    // move style name to descriptor
    if (node.key === 'style') node.descriptor = node.val['name']
    if (node.key === 'name' && node.parent.descriptor === node.val) return false

    return true
}

const tocDecorator = function (node) {
    if (node.val === null) return false
    if (node.val === undefined) return false

    // if it looks like a TOC entry...
    if (node.val['heading'] && (node.val['referentID'] || node.val['subheadings'])) {
        node.descriptor = node.val['heading']
    }
    if (node.key === 'heading' && node.parent.descriptor === node.val) return false

    // to decorate nested IR nodes
    return irDecorator(node)
}

/**
 * @param node
 * @return {boolean}
 */
const htmlDecorator = function (node) {
    //console.log('decorator', node.key, node.value, node)

    if (!node.parent) node.descriptor = 'root'

    // move Block type into descriptor
    //if (node.key === undefined) node.descriptor = node.value['tag']
    if (node.val['tag']) node.descriptor = node.val['tag']
    if (node.key === 'tag' && node.parent.descriptor === node.val) return false

    // skip white space only text nodes that occur between *every* HTML element
    if (node.val === '\n') return false

    return true
}

const parsePipeAndRender = function () {
    //todo[rollout, perf] need to make sure all the parsing and rendering
    // is done asynchronously. Perhaps ONLY update panes that are in view,
    // and update outdated panes when they come into view (e.g. tab pane is
    // selected).

    //todo[perf, rollout] we are recreating multiple tree views on every edit
    //  to the source. There is no need for that especially as there
    //  is only one view even visible at at time.
    //  OTOH, i've made the tree view code very optimal. It doesn't render the
    //  whole tree.
    //  ANYWAY, ask people, who may not have as fast a machine as i do, if TPL
    //  is a bear, and I can optimize this.

    // clear all the tree view panes:
    //  - so that if an exception occurs, worst case we'll see a blank pane,
    //    which is better than seeing the output of the previous input which
    //    would be confusing.
    //  - also because viewCustomSmartlyPrettifiedObject appends to
    //    rather than replaces their content
    //
    //    todo![rollout, api] if we add a `replace` arg to
    //     viewCustomSmartlyPrettifiedObject we wouldn't have to do this for
    //     the latter reason.
    stirJsonPane.replaceChildren()
    stirTextPane.replaceChildren()
    stirTreePane.replaceChildren()
    stirDebugPane.replaceChildren()
    tocPane.replaceChildren()
    console.log(previewBody)
    previewBody.replaceChildren()
    htmlPane.replaceChildren()
    htmlTreePane.replaceChildren()

    let stage
    let result
    try {
        stage = 'parsing'
        const stir = reader.parse(editor.getSession().getValue())
        stage = 'pipe'
        for (const pipe of pipes) {
            pipe(stir)
        }

        stirJsonPane.innerText = textplain.stir.toJSON(stir, 2)
        stirTextPane.innerText = stirwriter.render(stir)
        viewCustomSmartlyPrettifiedObject(stir, stirTreePane, irDecorator)
            .expandView(1, ['body'], true)
        viewCustomSmartlyPrettifiedObject(stir, stirDebugPane, null)
            .expandView(1, ['body'], true)

        let toc = stir?._tunnel?.toc
        if (toc === undefined) {
            // todo![now, references] can we show/hide the entire tab instead?
            toc = 'none generated'
        }
        viewCustomSmartlyPrettifiedObject(toc, tocPane, tocDecorator)
            .expandView(6, ['subheadings'], true)

        stage = 'rendering'
        result = writer.render(stir)
        previewBody.innerHTML = result

        // todo![now] should we do this change in the shadow DOM so that
        //   there isn't a moment between the above line and the code below
        //   where the links aren't intercepted? Or is that not possible because
        //   the JS event loop is single threaded and this entire method
        //   is thus atomic?
        /*
        IDEA 1: only allow nav to other plain text content
        --------------------------------------------------
        1. determine target content type (plain text vs html/other). Use file extension or fetch mimetype
        2. if not plain text, either prevent navigation, or offer to open in new window/tab
        3. if plain text, fetch and populate editor pane.
        - FUTURE: convert the <body> of the HTML to plain text and populate editor pane.
          - alert this user that this is what is happening (bc the plain text will be imperfect,
            and they need to know)
          - give them a choice: (a) open in new window, (b) convert to plain text or (c) cancel

        IDEA 2: Use Service Worker API 🌶
        ---------------------------------
        https://stackoverflow.com/a/43815800/8910547
        Seems designed for this:
        > Service workers essentially act as proxy servers that sit between web applications,
        > the browser, and the network (when available). They are intended, among other things,
        > to enable the creation of effective offline experiences, intercept network requests
        > and take appropriate action based on whether the network is available...


        IDEA 3: Override XMLHttpRequest
        -------------------------------
        https://stackoverflow.com/a/75641982/8910547
        https://stackoverflow.com/a/47282221/8910547

        IDEA 3: set CSP `navigate-to` policy to prevent off-site nav
        ----------------------------------------------
        https://developer.mozilla.org/en-US/docs/Web/HTTP/Headers/Content-Security-Policy
        (looks like this policy is experimental, same as the new Navigation API which would
        have been the idea solution.

        📥 once we have our interim solution, add a to-do to replace it with
         the [Navigation API](https://developer.mozilla.org/en-US/docs/Web/API/Navigation_API)
         once Firefox and Safari support it.
         See also https://developer.chrome.com/docs/web-platform/navigation-api/
        */
        previewBody.querySelectorAll('a').forEach(a => {

            a.addEventListener("click", (event) => {
                console.log('attampting request to:', a.href);
                const xhttp = new XMLHttpRequest();
                xhttp.open('HEAD', a.href);
                xhttp.onreadystatechange = function () {
                    if (this.readyState === this.DONE) {
                        console.log('  link status:', this.status);
                        console.log('  link content-type:', this.getResponseHeader("Content-Type"));
                    }
                };
                xhttp.send()
            })

            if (a.href.endsWith('.md')) {
                a.addEventListener("click", (event) => {
                    if (a.href.endsWith('.md')) {
                        event.preventDefault() // Block the link click from navigating
                    }
                    alert(`link clicked: ${a.href}`);
                })
            } else {
                a.setAttribute("target", "_blank")
            }
        })

        // todo[live] we may want to switch to this kind of non-collapsing html-tree
        //  view: http://software.hixie.ch/utilities/js/live-dom-viewer/#
        // todo[tpl, htmlnorm, rollout] whether it is normalized should be
        //  a TPL UI switch (and an opportunity to mention `htmlnorm`)
        const html = htmlwriter.render(stir)
        htmlPane.innerText = htmlnorm(html)
        viewCustomSmartlyPrettifiedObject(htmlparser(html), htmlTreePane, htmlDecorator)
            .expandView(2, ['content'], true)

        markSelection()

    } catch (e) {
        //todo[rollout] i think we want to deploy with the ability to call
        //  console.error from TPL code, but still filter out all console calls
        //  from the Textplain library. The only way this might work is if TPL
        //  doesn't roll Textplain into its own build, but treats it as an
        //  external library (ideally ESM). The Textplain library can have all
        //  console stuff stripped, but the TPL code can retain its.
        const stageErrorTitle = `${stage} error`
        console.error(stageErrorTitle, e)
        const DOWN_ARROW = '<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;⬇︎</p>'
        let unrolledMsg = `<h2>❌ ${stageErrorTitle}</h2>`
        let cause = e
        unrolledMsg += `${DOWN_ARROW}<p><strong>${cause.message}</strong></p>`
        while (cause.cause) {
            cause = cause.cause
            unrolledMsg += `${DOWN_ARROW}<p><strong>${cause.message}</strong></p>`
        }
        // stack trace for final cause only
        unrolledMsg += `<pre><code>${cause.stack}</code></pre>`

        if (stage === 'parsing') {
            const alert = '❌ parsing succeeded, but an error occurred rendering its output. See Rendering tab for info.'
            stirTreePane.insertAdjacentHTML('afterbegin', alert)
            stirJsonPane.insertAdjacentHTML('afterbegin', alert)
            stirTextPane.insertAdjacentHTML('afterbegin', alert)
            stirDebugPane.insertAdjacentHTML('afterbegin', alert)
        } else {
            stirTreePane.insertAdjacentHTML('afterbegin', unrolledMsg)
            stirJsonPane.insertAdjacentHTML('afterbegin', unrolledMsg)
            stirTextPane.insertAdjacentHTML('afterbegin', unrolledMsg)
            stirDebugPane.insertAdjacentHTML('afterbegin', unrolledMsg)
        }
        tocPane.insertAdjacentHTML('afterbegin', unrolledMsg)
        previewBody.insertAdjacentHTML('afterbegin', unrolledMsg)
        htmlPane.insertAdjacentHTML('afterbegin', unrolledMsg)
        htmlTreePane.insertAdjacentHTML('afterbegin', unrolledMsg)

        throw Error(stageErrorTitle, { cause: e })
    }
}

const syncScroll = function () {
    if (!syncScrolling) {
        return
    }
    // Not sure if this handles wrapped lines correctly (as well as folding and the like)
    const previewBody = $('#render-preview-frame').contents().find('body')[0]
    const lineNumber = editor.getFirstVisibleRow() + 1
    const matches = previewBody.querySelectorAll('*[data-src-line^=\'' + lineNumber + '-\']')
    if (matches.length > 0) {
        // is there ever more than one match? what does that mean? and why do we scroll to the first?
        matches[0].scrollIntoView()
    }
}

// todo[rollout] create a more pleasing glow and transition.
const markSelection = function () {
    syncScroll()
    let lineNumber = editor.getSelection().getCursor().row + 1
    const previewBody = $('#render-preview-frame').contents().find('body')
    const elt = previewBody.find('[data-src-line^=\'' + lineNumber + '-\']').last()
    previewBody.find('.selected').removeClass('selected')
    if (elt.length > 0) {
        elt.addClass('selected')
        setTimeout(function () {
            elt.removeClass('selected')
        }, 2000)
    }
}

function loadSample (selection) {
    if (selection === 'CLEAR') {
        editedInput = ''
        $('#sample-select option:contains(EDITED)').prop({ selected: true })
        selection = 'EDITED'
    }

    if (selection === 'URL') {
        let target = window.prompt('URL', 'https://raw.githubusercontent.com/vassudanagunta/htmlnorm/main/README.md')

        console.log('fetching', target)

        fetch(target).then(function (response) {
            console.log('status', response.status, response.statusText)

            // Examine the text in the response
            response.text().then(function (data) {
                //console.log(data)
                editedInput = data
                $('#sample-select option:contains(EDITED)').prop({ selected: true })
                selection = 'EDITED'
                editor.getSession().setValue(editedInput)
                editor.navigateFileEnd()
            })

        }).catch(function (error) {
            console.error(error)
            // todo[tpl] not sure this alert doesn't appear. adding
            //  "allow-modals" to the iframe sandbox initializer didn't work
            //  (at least the last time i tried).
            window.alert(error)
        })

        return
    }

    if (selection === 'EDITED') {
        editor.getSession().setValue(editedInput)
        editor.navigateFileEnd()
    } else {
        const path = 'samples/' + selection
        console.log('loading', path)
        $.get(path, function (data) {
            editor.getSession().setValue(data)
            lastChangeIsSampleLoad = true
            editor.navigateFileEnd()
        })
    }
}

